export default{
  props: {
    show: {
      type: Boolean,
      default: false,
      twoWay: true
    },
    title: {
      type: String,
      default: '提示'
    },
    dismissable: {
      type: Boolean,
      default: true
    },
    cancelable: {
      type: Boolean,
      default: true
    },
    cancel: {
      type: String,
      default: '取消'
    },
    confirm: {
      type: String,
      default: '确定'
    }
  },
  data() {
    return {
    };
  },
  watch: {
    show(val) {
      if (val) {
        $(this.$els.modal).modal('show');
      }else {
        $(this.$els.modal).modal('is active') && $(this.$els.modal).modal('hide');
      }
    }
  },
  methods: {
    onCancel() {
      this.show = false;
    },
    onConfirm() {
      this.$dispatch('on-confirm');
    }
  },
  ready() {
    // reset the show!
    $(this.$els.modal).modal({onHidden: ()=>{
      this.show = false;
    }});
  }
};


