export default {
  props: {
    title: {
      type: String
    },
    type: {
      type: String,
      default: 'info'
    },
    duration: {
      type: String,
      default: 3000
    },
    show: {
      type: Boolean,
      default: false,
      twoWay: true
    },
    dismissable: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    show(val) {
      if (this._timeout) clearTimeout(this._timeout);
      if (val && !!this.duration) {
        this._timeout = setTimeout(()=>this.show = false, this.duration);
      }
    }
  },
  computed: {
    icon() {
      switch (this.type) {
        case 'info':
          return 'meh';
        case 'warning':
          return 'warning';
        case 'success':
          return 'smile';
        case 'error':
          return 'frown';
        default :
          return '';
      }
    }
  },
};


