let self;
export default{
  props: {
    title: String,
    // text content
    content: String,
    // html content
    html: String,
    variation: String,
    position: {
      type: String,
      default: 'top left',
    }
  },
  ready() {
    self = this;
    $(self.$els.pop).popup({
      inline: true
    });
  }
};


